.hero {
  padding-top: 50px;
}

.hero__shadow {
  padding-bottom: 77px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left 0px bottom 0px;
  background-image: url(../images/hero/gradient-table.png);

  @media screen and(min-width:$tablet) {
    background-position: left 0px bottom 0px;
    background-image: url(../images/hero/gradient.png);
  }

  @media screen and(min-width:$desktop) {
    padding-bottom: 107px;
  }
}

.hero .container {
  @media screen and(min-width:$tablet) {
    padding: 0px 18px 0px 40px;
  }
}

.hero__position {
  position: relative;
  @media screen and(min-width:$tablet) {
    display: flex;
  }
  @media screen and(min-width:$desktop) {
    padding-bottom: 115px;
  }
}

.hero__container {
  @media screen and(min-width:$mobile-min) {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  @media screen and(min-width:$tablet) {
    align-items: flex-start;
  }
}
.hero__title {
  margin: 0px;
  padding: 0px;
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  margin-bottom: 30px;
  @media screen and(min-width:$tablet) {
    max-width: 332px;
    font-size: 26px;
  }

  @media screen and(min-width:$desktop) {
    max-width: 642px;
    font-size: 58px;
    margin-bottom: 24px;
  }
}

.hero__desc {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
  font-style: normal;
  font-size: 14px;
  line-height: 22px;
  margin: 0px;
  padding: 0px;
  @media screen and(min-width:$mobile-min) {
    margin-bottom: 30px;
  }

  @media screen and(min-width:$desktop) {
    margin-bottom: 40px;
    font-size: 20px;
  }

  &--text {
    font-weight: 500;
    color: $secondary-text-color;
  }

  &--topic {
    font-weight: bold;
    color: $dark-background-color;
  }

  &--label {
    font-weight: bold;
    color: $accent-color;
  }
}

.hero__button {
  width: 260px;
  height: 48px;
  padding: 14px 1px;
  margin-bottom: 30px;
  color: $inverse-text-color;
  background-color: $accent-color;
  text-decoration: none;
  text-align: center;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  border: none;

  @media screen and(min-width:$desktop) {
    width: 354px;
    height: 70px;
    padding: 27px 25px 26px 26px;
    margin-bottom: 0px;
    font-size: 14px;
  }
  &:hover,
  :focus {
    border: 2px solid $accent-color;
    background-color: #fff;
    color: $accent-color;
  }
}

.hero__thumb {
  width: 210px;
  height: 178px;
  margin-left: auto;
  margin-right: auto;
  @media screen and(min-width:$tablet) {
    position: absolute;
    top: 0px;
    right: 40px;
    width: 376px;
    height: 320px;
  }

  @media screen and(min-width:$desktop) {
    width: 670px;
    height: 572px;
  }
}

.hero__img {
  position: absolute;
  width: 178px;
  top: 0px;
  left: 20px;

  &--opacity {
    position: absolute;
    width: 208px;
    top: 10px;
    left: 0px;
  }

  @media screen and(min-width:$tablet) {
    width: 321px;
    height: 320px;
    top: 0px;
    right: 19px;
    left: auto;

    &--opacity {
      width: 376px;
      height: 269px;
      top: 17px;
      right: 0px;
      left: auto;
    }
  }

  @media screen and(min-width:$desktop) {
    width: 573px;
    height: 572px;
    top: 0px;
    right: 33px;
    &--opacity {
      width: 670px;
      height: 480px;
      top: 30px;
      right: 0px;
      left: auto;
    }
  }
}
